.top-row {
  display: flex;
  /* border: 2px solid green; */
}

.top-col {
  width: 50%;
  position: relative;
  padding: 2em 2em;
}

.top-col:nth-child(2) {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  overflow: hidden;
}

.top-col:nth-child(2)::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 95%;
  min-width: 450px;
  transform: translateX(35%);
  height: 300px;
  border-radius: 200px;
  background: var(--softblue);
}

.top-illustration-img {
  width: 100%;
  min-width: 400px;
  min-height: 300px;
  position: relative;
  z-index: 5;
  max-width: 550px;
}

.top-content-header {
  margin: 0.5em 0;
  font-size: 3rem;
}

.top-content-blurb {
  line-height: 1.5em;
}

.browser-btn {
  width: 135px;
  padding: 0.5em;
  margin: 1em;
  border-radius: 5px;
  border: 3px solid var(--grayblue);
  cursor: pointer;
  background: none;
  font-weight: 600;
  transition: 0.2s ease;
  position: relative;
  z-index: 5;
}

.browser-btn:hover {
  border: 3px solid var(--softblue);
  color: var(--softblue);
}

.browser-btn:hover::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 10%;
  top: 0;
  left: 0;
  transition: 0.2s ease;
  background: var(--softblue);
}

@media screen and (max-width: 785px) {
  .top-row {
    flex-direction: column;
    /* border: 2px solid red; */
  }

  .top-col {
    width: 100%;
    min-height: 450px;
    /* border: 3px solid green; */
  }

  .top-col:nth-child(1) {
    transform: translateY(85%);
  }

  .top-col:nth-child(2) {
    transform: translateY(-100%);
    align-items: flex-start;
    justify-content: center;
  }

  .top-col:nth-child(2)::after {
    content: "";
    position: absolute;
    bottom: 22%;
    right: 0;
    width: 95%;
    min-width: 450px;
    transform: translateX(35%);
    height: 265px;
    border-radius: 200px;
    background: var(--softblue);
  }
}
