.footer {
  padding: 1.5em;
  text-align: center;
  background: var(--darkblue);
  color: white;
}

.footer-logo {
  transition: 0.2s ease;
}

.footer-logo:hover {
  transform: translateY(-0.5em);
}

.footer-item {
  margin: 1.5em 0;
  text-transform: uppercase;
  cursor: pointer;
}

.footer-item:hover {
  font-weight: 700;
}

.footer-links {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-link {
  margin: 0 1em;
  cursor: pointer;
}

.footer-link:hover {
  transform: rotate(5deg);
}
