.navbar {
  padding: 2em 4em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.burger-icon {
  display: none;
  padding: 0.5em;
  cursor: pointer;
  transition: 0.5s ease;
}

.burger-icon:hover {
  transform: scale(1.5);
}

.nav-links {
  display: flex;
}

.nav-item {
  margin: 0 1em;
  transition: 0.2s ease;
  cursor: pointer;
  padding: 0.5em;
}

.nav-item:hover {
  color: var(--softred);
  text-shadow: 2px 3px 7px lightgray;
}

.login-item {
  border: 1px solid var(--grayblue);
  width: 80px;
  border-radius: 5px;
  text-align: center;
}

.login-item:hover {
  border: 1px solid var(--softred);
}

.mobile-menu {
  position: absolute;
  width: 100%;
  height: 500px;
  top: 0;
  left: 0;
  color: white;
  padding: 3em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  transition: 1s ease;
  z-index: 5;
}

.hide-mobile-menu {
  transform: translateX(150%);
  /* transition: 1s ease; */
  position: absolute;
}

.mobile-overlay {
  background: var(--darkblue);
  position: absolute;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 90%;
}

.mobile-overlay::after {
  content: "";
  width: 650px;
  height: 30em;
  border-radius: 100px;
  background: var(--grayblue);
  position: absolute;
  opacity: 15%;
  top: -8em;
  left: 0;
  transform: rotate(45deg) translateY(100px);
}

.top-mobile-row {
  display: flex;
  align-items: center;
  z-index: 5;
  position: relative;
  justify-content: space-between;
}

.close-icon {
  cursor: pointer;
  transition: 0.2s ease;
}

.close-icon:hover {
  transform: rotate(90deg);
}

.mobile-links {
  text-align: center;
  width: clamp(45%, 500px, 80%);
  margin: 0 auto;
  margin-top: 1.5rem;
}

.mobile-item {
  font-size: 1.5rem;
  padding: 0.75em;
  position: relative;
  min-width: 100px;
  cursor: pointer;
  transition: 0.2s ease;
}

.mobile-item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: lightgray;
  left: 0;
  top: -0.15em;
  opacity: 20%;
}

.mobile-item:hover::after {
  content: "--";
  font-size: 2rem;
  position: absolute;
  width: 55%;
  height: 85%;
  left: -30px;
  top: 15%;
  z-index: -1;
  margin: 0 auto;
}

.mobile-item:hover {
  transform: scale(1.03);
}

.login-mobile-item {
  border: 1px solid white;
  cursor: pointer;
  transition: 0.5s ease;
  margin-top: 0.25em;
  font-size: 1.5rem;
  padding: 0.75em;
}

.login-mobile-item:hover {
  background: white;
  color: var(--darkblue);
  border-radius: 100px;
}

.mobile-links,
.mobile-icons {
  position: relative;
  z-index: 5;
}

.mobile-icons {
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile-icon-link {
  margin: 0 1em;
  cursor: pointer;
  transition: 0.2s ease;
}

.mobile-icon-link:hover {
  filter: blur(3px);
}
@media screen and (max-width: 725px) {
  .nav-links {
    opacity: 0;
    pointer-events: none;
    width: 0px;
    overflow: hidden;
  }

  .burger-icon {
    display: block;
  }

  .mobile-overlay::after {
    content: "";
    width: 650px;
    height: 20em;
    border-radius: 100px;
    background: var(--grayblue);
    position: absolute;
    opacity: 15%;
    top: -2em;
    left: 0;
    transform: rotate(45deg) translateY(100px);
  }
}

@media screen and (max-width: 450px) {
  .mobile-overlay::after {
    content: "";
    width: 450px;
    height: 20em;
    border-radius: 100px;
    background: var(--grayblue);
    position: absolute;
    opacity: 15%;
    top: -4em;
    left: -2em;
    transform: rotate(45deg) translateY(100px);
  }
}
