.middle-row {
  display: flex;
  padding: 1em 0;
}

.middle-col {
  width: 50%;
  position: relative;
  padding: 2em 2em;
}

.middle-col:nth-child(2) {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  overflow: hidden;
}

.middle-col:nth-child(1)::after {
  content: "";
  position: absolute;
  bottom: 0%;
  left: 0;
  width: 800px;
  min-width: 450px;
  transform: translateX(-70%);
  height: 50%;
  min-height: 300px;
  border-radius: 200px;
  background: var(--softblue);
  /* background: red; */
}

.middle-illustration-img {
  width: 100%;
  min-width: 300px;
  min-height: 250px;
  position: relative;
  z-index: 5;
  max-width: 550px;
}

.top-content-header {
  margin: 0.5em 0;
  font-size: 3rem;
}

.top-content-blurb {
  line-height: 1.5em;
}

.info-btn {
  width: 135px;
  padding: 0.5em;
  margin: 1em;
  border-radius: 5px;
  border: 3px solid var(--grayblue);
  cursor: pointer;
  background: none;
  font-weight: 600;
  transition: 0.2s ease;
  position: relative;
  z-index: 5;
}

.info-btn:hover {
  border: 3px solid var(--softblue);
  color: var(--softblue);
}

.browser-btn:hover::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 10%;
  top: 0;
  left: 0;
  transition: 0.2s ease;
  background: var(--softblue);
}

@media screen and (max-width: 785px) {
  .middle-row {
    flex-direction: column;
    /* border: 2px solid red; */
    /* border: 2px solid red; */
  }

  .middle-col {
    width: 100%;
    /* min-height: 450px; */
    /* border: 3px solid green; */
  }

  .middle-col:nth-child(2) {
    /* transform: translateY(-100%); */
    align-items: flex-start;
    justify-content: center;
  }

  .middle-col:nth-child(1) {
    /* transform: translateY(75%); */
  }

  .middle-col:nth-child(1)::after {
    content: "";
    position: absolute;
    bottom: 20%;
    left: 0;
    width: 800px;
    min-width: 450px;
    transform: translateX(-70%);
    height: 50%;
    min-height: 200px;
    border-radius: 200px;
    background: var(--softblue);
    /* background: red; */
  }
}
