.subscribe-card {
  text-align: center;
  padding: 4em 0em;
  width: 100%;
  border: 3;
  background: var(--softblue);
  color: white;
}

.subscribe-h3,
.subscribe-h1 {
  padding: 1em;
  font-weight: 500;
}

.subscribe-h3 {
  text-transform: uppercase;
  letter-spacing: 0.35em;
}

.subscribe-h1 {
  font-size: 2.5rem;
}

.subscribe-input-row {
  /* display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative; */
  /* border: 6px solid blue; */
  width: clamp(40%, 500px, 90%);
  margin: 0 auto;
  height: 125px;
  /* padding: 1.5em; */
}

.subscribe-input-parent {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  position: relative;
  /* border: 3px solid purple; */
  width: 100%;
  margin: 0 auto;
  /* padding: 1.5em; */
}

.subscribe-input-div {
  width: 70%;
  position: relative;
  /* border: 3px solid green; */
}

.email-input {
  padding: 1em;
  /* border: 2px solid red; */
  width: 100%;
  border-radius: 4px;
}

.error-h5 {
  width: 100%;
  /* border: 2px solid blue; */
  text-align: start;
  padding: 0.25em 1em;
  background: var(--softred);
  color: white;
  position: absolute;
  animation: fadein 0.5s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.error-icon {
  position: absolute;
  top: 30%;
  right: 5%;
}

.btn-container {
  width: 25%;
}

.subscribe-btn {
  padding: 1em;
  text-align: center;
  margin-left: 1rem;
  background: white;
  cursor: pointer;
  font-weight: 700;
  border-radius: 5px;
  width: 25%;
  color: red;
  border: 2px solid red;
}

@media screen and (max-width: 750px) {
  .subscribe-input-row {
    padding: 0;
    height: 125px;
  }

  .subscribe-input-parent {
    flex-direction: column;
    display: flex;
  }

  .subscribe-input-div {
    width: 100%;
  }

  .subscribe-btn {
    width: 100%;
    margin-left: 0em;
    transform: translateY(2em);
  }
}
