.faq-parent {
  padding: 4em 0em;
}

.faq-card {
  width: clamp(50%, 450px, 90%);
  /* border: 2px solid red; */
  margin: 0 auto;
  text-align: center;
}

.faq-h1 {
  padding: 0.5em 0;
}

.faq-item {
  padding: 0.5em;
}

.faq-question {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1em;
  border-bottom: 1px solid lightgray;
  border-top: 1px solid lightgray;
}

.arrow-icon {
  cursor: pointer;
}

.red {
  color: red;
}

.answer-p {
  text-align: start;
  line-height: 1.85em;
  padding: 1em;
  transition: 0.5s ease;
  height: 100%;
  animation: show 0.5s forwards;
}

.hide-answer {
  overflow: hidden;
  transition: 0.5s ease;
  padding: 0em;
  height: 0px;
  animation: hide 0.5s forwards;
}

@keyframes show {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes hide {
  0% {
    transform: scale(1);
  }

  100% {
    height: 0px;
    transform: scale(0);
  }
}
