.features-parent {
  text-align: center;
  padding: 2em;
}

.features-p {
  width: clamp(40%, 400px, 85%);
  margin: 0 auto;
  line-height: 1.5em;
  padding: 1em 0;
}

.menu-row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.middle-row {
  filter: blur(5px);
  transition: 2s ease;
  animation: showinfo 1s forwards;
}

@keyframes showinfo {
  0% {
    filter: blur(5px);
  }

  100% {
    filter: blur(0px);
  }
}

.menu-title-column {
  border-bottom: 3px solid gray;
  cursor: pointer;
}

.menu-title {
  margin: 1em 1.5em;
}

.menu-title-column:hover {
  color: red;
}

.menu-active {
  border-bottom: 3px solid red;
}

@media screen and (max-width: 750px) {
  .menu-row {
    flex-direction: column;
  }

  .menu-title-column {
    width: 85%;
    text-align: center;
  }
}
