.browser-parent {
  padding: 5em 2em;
  transform-style: preserve-3d;
}

.browser-header {
  text-align: center;
}

.browser-h1 {
  padding: 1em 0;
}

.browser-p {
  width: clamp(40%, 500px, 85%);
  margin: 0 auto;
  line-height: 1.5em;
}

.browser-body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  perspective: 5px;
  transform-style: preserve-3d;

  /* border: 2px solid red; */
}

.b-card {
  text-align: center;
  /* border: 2px solid blue; */
  flex: 1;
  margin: 0.5em;
  box-shadow: 2px 3px 7px gray;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.5s ease;
}

.b-card:nth-child(2) {
  transform: translateY(2em);
}

.b-card:nth-child(3) {
  transform: translateY(4em);
}

.b-card:hover {
  background: radial-gradient(lightblue, violet);
  /* transform: translateZ(0.25px); */
  filter: hue-rotate(90deg);
}

.b-header-content {
  height: 75%;
  padding: 2em 0.5em;
  /* border: 2px solid green; */
}

.b-header-bottom {
  height: 25%;
  /* border: 2px solid red; */
  width: 100%;
  padding: 1em;
}

.browser-btn {
  background: var(--softblue);
  color: white;
  padding: 0.5em;
}

.browser-btn:hover {
  color: white;
  text-decoration: underline;
}

@media screen and (max-width: 650px) {
  .browser-body {
    flex-direction: column;
  }

  .b-card {
    width: clamp(30%, 400px, 75%);
  }
}
